import { Injectable, OnDestroy } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Storage } from '@ionic/storage';
import { IProduct } from './data/iproduct';
import { Observable, Subject, Subscription } from 'rxjs';
import { IOverview } from './data/ioverview';

@Injectable({
  providedIn: 'root'
})
export class ProductsService {
  public static readonly APIUrl = "https://de.openfoodfacts.org/api/v0/product/[].json"
  public productSubject = new Subject<IProduct>();
  

  // don't automatically do something on every page, for performance and code cleanness reasons
  constructor(private storage: Storage, private httpClient: HttpClient) { }

  productListChange(): Observable<any> {
    return this.productSubject.asObservable();
  }
  onProductListUpdate() {
    this.productSubject.next();
  }
  public async getAllProducts(): Promise<Array<IProduct>> {
    const products = await this.storage.get("products")
    if (products) {
      return products;
    }
    else {
      return null;
    }
  }
  /**
   * This function is where all the magic happens.
   * Although it looks like it is not very well written, it is actually quite optimized for performance.
   * @param timespan The timespan in which products are grouped.
   */
  public async getProductsByTimespan(timespan: number): Promise <any> {
    const all = await this.getAllProducts();
    const final: Array<any> = [];
    if (all === null) {
      return null;
    }
    const filtered = this.getDataByTimespan(all, timespan);
    for (const key in filtered) {
      if (Object.prototype.hasOwnProperty.call(filtered, key)) {
        // tslint:disable-next-line: no-shadowed-variable
        const timespan = filtered[key];
        const overviewItem: IOverview = {items: timespan, range: key};
        final.push(overviewItem);
      }
    }
    return final;
  }
  public getDataByTimespan(data: Array<IProduct>, timespan: number): Array<Array<IProduct>> {
    const timespanData = [];
    for (const current of data){
      switch (timespan) {
        case 0:
          const ym = current.addDate.toLocaleString('default', { month: 'long' }) + ' ' + current.addDate.getFullYear();
          if (!timespanData[ym]) {
            timespanData[ym] = [];
          }
          timespanData[ym].push(current);
          break;
        case 1:
          const firstDayOfYear = new Date(current.addDate.getFullYear(), 0, 1);
          // type any because typescript doesn't support abstractions like this.
          const pastDaysOfYear = (current.addDate as any - (firstDayOfYear as any)) / 86400000;
          const w = Math.ceil((pastDaysOfYear + firstDayOfYear.getDay() + 1) / 7);
          if (!timespanData[w]) {
            timespanData[w] = [];
          }
          timespanData[w].push(current);
          break;
      }
    }
    return timespanData;
  }
  public async getProductDetails(barcode: string): Promise<IProduct> {
    const result = await this.httpClient.get(ProductsService.APIUrl.replace('[]', barcode))
      .toPromise() as any;
    if (result && result.status === 1 && result.product) {
      return result.product;
    }
    else {
      throw new Error("Product not found");
    }
  }
  public async addProduct(product: IProduct): Promise<boolean> {
    // var product = await this.getProductDetails(barcode);
    if (product !== null) {
      product.addDate = new Date();
      let all = await this.getAllProducts();
      all.push(product);
      this.storage.set("products", all)
      this.onProductListUpdate();
      return true;
    }
    else {
      return false
    }
  }
}
